@mixin button { 
	display          : inline-block;
	padding          : .7rem 1rem;
	background-color : #007ff4;
	color            : #FFF;
	cursor           : pointer;
	text-decoration  : none;
	line-height      : inherit;
	border-radius    : 40px;
	transition       : transform .05s;
	text-align       : center;
	user-select      : none;
	margin-bottom: 10px;
  
	&:hover {
		background-color : #ffffff;
		color: #007ff4;
		text-decoration  : inherit;
		-webkit-box-shadow: 0px 1px 22px -2px rgba(0,0,0,0.41); 
		box-shadow: 0px 1px 22px -2px rgba(0,0,0,0.41);
	}
  
	&:active {
		background-color : #ffffff;
		border: 1px solid #007ff4;
		color: #007ff4;
		text-decoration  : inherit;
	}
  }
